import * as React from 'react'
import { Col, Grid, Row } from 'react-bootstrap'
import NavMenu from './nav'
import MsNotice from './msNotice'

export default props => (
  <Grid fluid>
    <Row>
      <Col sm={3}>
        <NavMenu />
      </Col>
      <Col sm={9}>
        {props.children}
      </Col>
    </Row>
    <MsNotice />
  </Grid>
)



